g:.7rem 1.5rem 1.5rem 1.5rem;position:absolute;right:0;top:0;transform:rotateY(180deg)}.qq__card .qq__card__code img{padding:.3rem;background:#fff;border-radius:8px;width:5.2rem;position:absolute;right:1.5rem;top:.5rem}.qq__card .qq__card__code font{display:block;font-size:14px;font-weight:700}.qq__card__name{transition:transform 1s ease-in-out;transform-origin:center center}.qq__card__code{position:absolute;top:0;left:0;opacity:0}.qq__card:hover .qq__card__name{animation:rotateAndFadeOut 1s forwards}.qq__card:hover .qq__card__code{z-index:1;animation:fadeIn .5s forwards .5s}@keyframes rotateAndFadeOut{0%{transform:rotateY(0deg)}50%{transform:rotateY(90deg)}100%{transform:rotateY(180deg)}}@keyframes fadeIn{0%{opacity:1}100%{opacity:1}}.webinfo-item-title{display:flex;align-items:center;font-size:16px}.webinfo-item-title i{font-size:16px;width:16px;margin-right:6px;line-height:2}.menu-title-box h3{margin-bottom:10px}.menu-title-box h3 i{position:relative;top:1px}.menu-title-box-flex{display:flex;justify-content:space-between;margin:15px 0 0}.menu-title-box-index{background:var(--contentBgColor);border:var(--style-border);color:var(--text-color);border-radius:8px;margin-right:4px;width:25px;height:25px;display:flex;align-items:center;justify-content:center;line-height:1;margin-top:2px}.menu-title-box-t{width:calc(100% - 30px);font-weight:500;padding:0 6px;font-size:15px}.menu-title-box-t:hover{cursor:pointer;color:var(--yellow);transform:scale(1.05);transition:.5s all}.menu-title{padding-left:.3rem;transition:all .2s ease-in-out 0s;line-height:24px;padding:8px;border-radius:8px;color:var(--text-color);cursor:default;min-height:40px;display:flex;align-items:center;cursor:pointer;font-size:1.1em;opacity:.8;filter:blur(0.8px)}.menu-title:hover{color:var(--yellow);background:rgba(152,162,169,.0509803922);font-size:1.1em;opacity:1;filter:blur(0)}.menu-title.active{color:var(--yellow);font-size:1.3em;font-weight:700;filter:blur(0) !important;opacity:1}